<template>
    <v-card
    class="mx-auto rounded-10" 
    max-width="400"
    elevation="5"
    >
        <v-carousel
            class="pt-5"
            cycle
            height="200"
            show-arrows-on-hover
            hide-delimiters
        >
            <v-carousel-item
            v-for="(screen, i) in screens"
            :key="i"
            >
                <div align="center">
                    <img :src="screen" class="carousel-imgs"/>
                </div>
            </v-carousel-item>
        </v-carousel>
        <v-card-title>{{softwareName}}</v-card-title>
        <v-card-text>
            {{description}}
            <div v-if="externalURL!=null">
                <a :href="externalURL" target="Starfall">{{hyperlinkText}}</a>
            </div>
        </v-card-text>
        <v-menu
            :close-on-content-click="false"
            :nudge-width="200"
            max-width="300px"
            absolute
            >
            <template v-slot:activator="{ on }">
                <v-row class="pa-0 ma-0" justify="center">
                    <v-btn 
                    small 
                    :color="$accentColor"
                    v-on="on"
                    light>
                    Ver info. de desarrollo
                    </v-btn>
                </v-row>
            </template>

            <v-card :color="$accentColor" light>
                <v-card-title>
                    {{softwareName}}
                </v-card-title>
                <v-card-text class="pb-0">
                    <p class="title ma-0">Front-end</p>
                </v-card-text>
                <v-row class="pa-0 ma-0">
                    <v-col cols="6" class="pa-0 ma-0">
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title>Framework</v-list-item-title>
                                <v-list-item-subtitle>{{frontendFramework}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                    </v-col>
                    <v-col cols="6" class="pa-0 ma-0">
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title>Lenguaje</v-list-item-title>
                                <v-list-item-subtitle>{{frontendLanguage}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                    </v-col>
                </v-row>
                <v-card-text class="pb-0 pt-0">
                    <p class="title ma-0">Back-end</p>
                </v-card-text>
                <v-row class="pa-0 ma-0">
                    <v-col cols="4" class="pa-0 ma-0">
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title>Framework</v-list-item-title>
                                <v-list-item-subtitle>{{backendFramework==null?'Ninguno':backendFramework}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                    </v-col>
                    <v-col cols="4" class="pa-0 ma-0">
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title>Lenguaje</v-list-item-title>
                                <v-list-item-subtitle>{{backendLanguage==null?'Ninguno':backendLanguage}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                    </v-col>
                    <v-col cols="4" class="pa-0 ma-0">
                        <v-list-item>
                            <v-list-item-content>
                                <v-list-item-title>DB</v-list-item-title>
                                <v-list-item-subtitle>{{backendDB==null?'Ninguna':backendDB}}</v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                    </v-col>
                </v-row>
            </v-card>
        </v-menu>
        <v-row class="pa-0 ma-0">
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-title class="subtitle-1 font-weight-bold"></v-list-item-title>
                </v-list-item-content>
            </v-list-item>
        </v-row>
    </v-card>
</template>
<script>
export default {
    props:{
        screens:[],
        softwareName:String,
        description:String,
        externalURL:String,
        hyperlinkText:String,
        frontendFramework:String,
        frontendLanguage:String,
        backendFramework:String,
        backendLanguage:String,
        backendDB:String
    }
}
</script>
<style scoped>
    .carousel-imgs {
    width: 200px;
    height: 400px;
    object-fit:cover;
    }
</style>